.flex11 .blockText3 {
	position:relative;
	padding:0 15px;
}
.blockText3 h2 {
	font:normal 3.6rem 'AvenirNextCyr';
	line-height:58px;
	text-align: center;
	letter-spacing: 0.05em;
	text-transform: uppercase;
	color: #F4A876;
	margin:15px auto;
}
.blockText3 .text3 {
	font:normal 1.8rem 'AvenirNextCyr';
	line-height: 25px;
	color: #5C5C5C;
	margin:10px;
	text-align: left;
}
.blockText3 ul {
	margin:10px;
}
.blockText3 ul li {
    font:normal 1.8rem 'AvenirNextCyr';
	line-height: 25px;
	color: #B5B5B5;
}
.flex11 .imgPost {
	display: flex;
	flex-flow: wrap;
	width: fit-content;
    margin: 15px auto;
}
.wrapper .imgPost img {
	width: auto;
	height: 280px;
}


/*---------------------- @media 1360 --------------------------*/
@media screen and (min-width:1360px) {
	.flex11 .blockText3 {
		width: 80%;
		margin:15px auto;
	}
}





