/*----------- product_card ------------*/
.mainContent .cardProduct {}
.cardProduct .flex14 {
	display:flex;
	flex-direction:column;
	flex-wrap:wrap;
	border-bottom: .1rem solid #E5E5E5;
}
.flex14 .blockLeft {
	margin: 0 10px;
}
.flex14 .blockLeft,
.flex14 .blockRight {
	width:100%;
	margin:0 auto;
}
.flex14 .tabs {
	height:200px;
	margin:15px auto;
}
.bread .homeLink {
	display:inline-block;
	width:15px;
	height:15px;
	background:url('../img/bread_home_img.png') center no-repeat;
}
.tabs .listItemProd {
	display:flex;
	width: 100%;
	justify-content: space-between;
}
.listItemProd a {
	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 110%;
	letter-spacing: 0.05em;
	color: #5C5C5C;
}
.blockTable td {
	width: auto;
}
.blockLeft .blockImg2 {
	display: flex;
	width:300px;
	flex-flow: wrap;
	justify-content: space-between;
}
.blockImg2 .imgProd3 {
	width: 300px;
	margin: 2px;
}
.blockImg2 .mini {
	width: 95px;
}
.blockSize .sizeProd {
	width:34px;
	height:31px;
	background: #FFFFFF;
	border: 1px solid #E5E5E5;
	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 19px;
	text-align: center;
	color: #5C5C5C;
	padding:5px 0;
}
.blockRight .blockUp {
	display: flex;
	flex-flow: wrap;
	margin: 0 10px;
    padding-bottom: 10px;
    border-bottom: .1rem solid #F0F0F0;
}
.blockUp h1 {
	font:normal 2.7rem 'AvenirNextCyr';
	line-height: 44px;
	color: #5C5C5C;
	width: 95%;
	text-align: left;
}
.blockSize h4 {
	margin:10px 0;
}
.blockSize ul {
	display: flex;
	flex-flow: wrap;
	margin:10px 0;
}
.blockSize ul li {
	margin:5px;
}
.blockUp .yesProd1 {
	width: auto;
	height: max-content;
	border: 1px solid #52BD10;
	padding:10px;
	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 110%;
	color: #52BD10;
}
.blockRight .blockMedium {
	display: flex;
	align-items:center;
}
.blockMedium .blockSize,
.blockMedium .blockPrice {
	width: 50%;
	margin: 10px;
}
.blockMedium1 .blockPay1 .payLink1 {
	display: inline-block;
}
.blockMedium .blockPrice {
	/*width: 120px;*/
	margin: 10px;
}
.blockMedium .blockPrice .size1 {
	font-size: 3rem;
}
.blockPrice .blockOldSize {
	display: flex;
	align-items: center;
	justify-content:flex-end;
}
.blockMedium .blockPrice .oldSize1 {
	margin-right: 10px;
	text-align: right;
}
.blockOldSize .labelSale {
	border-radius:50%;
	width: 30px;
	height:30px;
	background: url('../img/labelSale_icon.png') center no-repeat;
}
.blockMedium .blockPrice p {
	text-align: right;
}
.blockRight .blockMedium1 {
	display: flex;
	flex-flow: wrap;
	margin:5px;
	justify-content: flex-end;
}
.blockMedium1 .blockPay1 .payLink1 {
	text-align:right;
	background:#F4A876;
	color:#fff;
}
.formCallback #backPhone {
	display: flex;
	flex-flow: wrap;
	margin-bottom: 10px;
}
#backPhone input {
	width: 50%;
	padding: 15px;
}
#backPhone input[type="tel"] {
	border: 1px solid #E5E5E5;
	text-align: center;

	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 110%;
	color: #5C5C5C;
}
#backPhone input[type="submit"] {
	background: #fff;
	border: 1px solid #F4A876;
	font:normal 1.6rem 'AvenirNextCyr';
	line-height: 110%;
	color: #F4A876;
}
#backPhone input[type="submit"]:hover {
	color: #ffffff;
	background: #F4A876;
	box-sizing: border-box;
	box-shadow: 11px 2px 30px rgba(244, 168, 118, 0.25);
}
.blockRight .blockTable {
	margin:15px 0;
}
.blockTable table {
	width: 100%;
	text-align: left;
	background: rgba(244, 168, 118, 0.05);
}
.blockTable table {
	font:normal 1.2rem 'AvenirNextCyr';
	line-height: 19px;
	color: #C4C4C4;
	padding: 10px;
}
.blockTable table tr {
	margin:10px 0;
}
.blockTable table td {
	margin:0;
}
.blockTable table tr td:last-child {
	text-align: left;
}
.tabs .listItemProd {
	margin-bottom: 20px;
}
.listItemProd li {
	padding:10px;
}
#fragment-4 .textEmail input {
	border: 1px solid #E5E5E5;
	padding:15px;
}
/*.ui-tabs-tab, .ui-corner-top, .ui-state-default, .ui-tab,*/
.ui-tabs-active,
.ui-state-active,
.ui-state-focus {
	border: 1px solid #F4A876;
	color:#F4A876 ;
}
section .blockSimilarProd,
section .blockViewedProd {
	display:flex;
	justify-content: space-between;
	flex-wrap:wrap;
}

.similarProducts h3,
.viewedProducts h3 {
	margin:50px 0 30px 0;
}

.blockSimilarProd .article1,
.blockViewedProd .article1 {
	width:300px;
	position: relative;
	margin:5px 0; // сделали замену с auto на 0
	//padding:10px; ///////// убрать от сюда и добавть к каждому под-блоку
	background: #FFFFFF;
	border: 1px solid #F0F0F0;
   transform-origin: 5px 10px;
   transition:all 1s ease-out;
}
.blockSimilarProd .article1:hover,
.blockViewedProd .article1:hover {
  z-index: 6;
  transform: scaleY(1.02);
  box-shadow: 10px 20px 70px rgba(244, 168, 118, 0.2);
  border:none;
}
.blockSimilarProd .article1:hover table,
.blockViewedProd .article1:hover table {
  display: table;
  //box-shadow: 10px 20px 70px rgba(244, 168, 118, 0.2);
}
.blockSimilarProd .article1 .textProd1,
.blockViewedProd .article1 .textProd1 {
	 position: relative;
}
.blockSimilarProd .article1 table,
.blockViewedProd .article1 table {
    display: none;
    position: absolute;
    z-index: 5;
    padding:0 10px;
    background: #fff;
}





/*---------------------- @media --------------------------*/
@media screen and (min-width:667px) {
	.blockRight .blockMedium1 {
		justify-content:space-between;
	}
	.blockRight .blockUp {
		flex-flow: row;
		justify-content: space-between;
	}
	.blockUp h1 {
		width: 70%;
	}
}
@media screen and (min-width:768px) {
	.listItemProd a {
		font-size: 1.8rem;
	}
	.blockTable table {
		font-size: 1.4rem;
	}
}
/*              @media 1024px                */
@media screen and (min-width:1024px) {}
/*              @media 1280px                */
@media screen and (min-width:1280px) {
	.cardProduct .flex14 {
		flex-direction:row;
		flex-wrap:nowrap;
		padding-bottom: 50px;
	}
	.flex14 .blockLeft,
	.flex14 .blockRight {
		width:50%;
	}
	.flex14 .blockRight {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
}
/*              @media 1360px                */
@media screen and (min-width:1360px) {}