.sliderProd ul {
  margin: 0;
  padding: 0;
}
.sliderProd ul li {
  //width: 90%;
}

/* Кнопки управления */
.bx-wrapper {
  position: relative;
}
.sliderProd .bx-controls-direction {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 536px;
}
.sliderProd .bx-controls-direction a:hover {
  filter: brightness(90%);
}
.sliderProd .bx-prev {
  background: url(/prev.png) 0 0 no-repeat;
  display: inline-block;
  width: 30px;
  height: 30px;
  position: absolute;
  margin-top: -15px;
  top: 50%;
  left: 30px;
  overflow: hidden;
  text-indent: 999px;
  z-index: 1000;
}
.sliderProd .bx-next {
  background: url(/next.png) 0 0 no-repeat;
  display: inline-block;
  width: 30px;
  height: 30px;
  position: absolute;
  margin-top: -15px;
  top: 50%;
  right: 30px;
  overflow: hidden;
  text-indent: 999px;
  z-index: 1000;
}


/* Превью */
.slider_pager ul {
  display: flex;
  margin: 0;
  padding: 0;
}
/*.slider_pager ul li {
  display:;
}*/
.slider_pager li {
  margin: 0 1px 10px 0;
  display: inline-block;
}
.slider_pager li a img {
  border: 1px solid #ddd;
  width: 100%;
  height: auto;
}
.slider_pager .active img {
  border: 1px solid rgb(5, 70, 162);
}