/*-------------- CATALOG ------------*/
.wrapper .rowContainer {
	display:flex;
	flex-flow: wrap;
}
.mainContent .banner {
	background: url('../img/catalog_img.png') center no-repeat;
	background-size: cover;
}
.blockText4 h1 {
	font:normal 600  2.8rem 'AvenirNextCyr';
	line-height: 38px;
	letter-spacing: 0.05em;
	color: #FFFFFF;
}
.blockText4 p {
	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 19px;
	color: #FFFFFF;
}
.rowContainer h3 {
	font:normal 1.8rem 'AvenirNextCyr';
	line-height: 25px;
	letter-spacing: 0.05em;
	color: #5C5C5C;
	display: flex;
	align-items: center;
}
.gridContainer .breadcrumb {
	display:flex;
	margin:30px auto;
	align-items:center;
}
.breadcrumb .orangeDots {
	color:#F4A876;
}
.breadcrumb li {
	padding: 5px;
}
.breadcrumb li span {
	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 19px;
	letter-spacing: 0.1em;
	color: #C4C4C4;
}
.sidebar .sideMenu,
.sidebar .sidearea {
	margin:30px 10px;
}
.listSubCat .itemSubCat {
	display: flex;
	margin:14px 0;
	align-items: center;
}
.sideMenu .lineActCat {
	display: block;
	width: 29px;
	height: 1px;
	margin-right: 6px;
	background: #F4A876;
}
.sideMenu .linkSubCat {
	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 19px;
	color: #C4C4C4;
}
.sideMenu .linkSubCat:hover {
	color: #5C5C5C;
}
.sideMenu .linkSubCat:active {
	color:#F4A876;

}
.mainContent .banner {
	padding:50px 10px;
	width: 100%;
}
.blockText4 h1 {
	position: relative;
	margin-bottom: 30px;
}
.blockText4 h1,
.blockText4 p {
	text-align: left;
}
.blockText4 h1::after {
	position: absolute;
	content:'';
	width: 46px;
	height: 3px;
	background: #F4A876;
	bottom: -16px;
	left:0;
}
.sidearea .iconFiltr {
	margin-right:8px;
	display: block;
	width: 19px;
	height:19px;
	background:url('../img/filter_icon.svg') no-repeat center;
}
.sidearea .iconArrow {
	margin-left: 5px;
	display: block;
	width:12px;
	height:12px;
	background:url('../img/filtr_arrow.svg') no-repeat center;
}

.blockSortProd .select {
	font:normal 1.4rem 'AvenirNextCyr';
	line-height: 19px;
	color: #F4A876;
	border: none;
}

/////////////////  новые правки
.mainContent .blockProduct {
	display:flex;
	flex-flow: wrap;
	justify-content: space-between;
}
.blockProduct .article1 {
  width:300px;
  position: relative;
  margin:5px auto; // сделали замену с auto на 0
  //padding:10px; ///////// убрать от сюда и добавть к каждому под-блоку
  background: #FFFFFF;
  border: 1px solid #F0F0F0;
  transform-origin: 5px 10px;
  transition:all 1s ease-out;
}
.blockProduct .article1:hover {
  z-index: 6;
  transform: scaleY(1.01);
  box-shadow: 10px 20px 70px rgba(244, 168, 118, 0.2);
  border:none;
}
.blockProduct .article1:hover table {
  display: table;
  //box-shadow: 10px 20px 70px rgba(244, 168, 118, 0.2);
}
.blockProduct .article1 .textProd1 {
	 position: relative;
}
.blockProduct .article1 table {
    display: none;
    position: absolute;
    z-index: 5;
    padding:0 10px;
    background: #fff;
}
.textProd1 .maybeProd i {
	 position: inherit;
}
.textProd1 h4 a {
	font:normal 1.6rem 'AvenirNextCyr';
	line-height: 22px;
	color: #787878;
}
.textProd1 h4 a:hover {
	color:#F4A876;
}

/*---------------------- @media 1360 --------------------------*/
@media screen and (min-width:1360px) {
    .mainContent .banner {
    	background: url('../img/catalog_img2.jpg') center no-repeat;
    	background-size: cover;
    	height: 285px;
    	padding: 91px 0 0 119px;
	}
	.wrapper .rowContainer {
		flex-wrap: nowrap;
		//align-items: center;
	}
	.sidebar .sideMenu,
	.sidebar .sidearea {
		margin:0 10px;
	}
	.banner .blockText4 {
		width: 683px;
	}
	.blockText4 h1::after {
		width: 134px;
	}
	.rowContainer .sidebar {
		width: 328px;
	}
	.sidearea .iconArrow {
		display: none;
	}
	.blockProduct .article1 {
		margin:5px 0;
	}
}












